.ModalBackground {
    background-color: #000000c7;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    /* z-index: 1000; */

    display: flex;
    align-items: center;
    justify-content: center;
}

.ModalInput {
    width: 70vw;
    min-height: 300px;
    background-color: white;
    color: black;
    padding: 20px 20px;
    border-radius: 7px;
    /* z-index: 2000; */
}

.WordInput {
    box-sizing: border-box;
    padding: 6px;
    font-size: 3rem;
    width: 100%;
    text-align: center;
}

.InputError {
    color: #dd1414;
    font-weight: 600;
    font-size: 0.8em;
}